.ticket-container {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 125px;
    margin: 0;
    overflow: auto;
    padding: 0;
    text-transform: uppercase;
    width: 375px;

    background-color: #FDF4EC;

    p {
        margin: 0;
        padding: 0;
    }

    .left-container {
        display: flex;
        justify-content: space-between;
        height: 100%;

        p {
            align-items: center;
            display: flex;
            line-height: 9px;
            font-size: 9px;
            margin: 0;
            padding: 0;
            transform: rotate(-90deg);
        }

        .seating-info {
            display: flex;
            flex-direction: column;
            width: 18px;
            height: 100%;

            .text-container {
                align-items: center;
                display: flex;
                flex-direction: column-reverse;
                justify-content: space-around;
                height: 100%;
            }
        }

        .ticket-border {
            align-items: center;
            background-color: #F48722;
            border: none;

            display: flex;
            flex-direction: column;
            width: 18px;
            height: 100%;

            .text-container {
                align-items: center;
                display: flex;
                flex-direction: column-reverse;
                justify-content: space-around;
                height: 100%;
            }
        }
    }

    .ticket-center {
        align-items: center;
        display: inline-block;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        overflow: auto;
        text-overflow: ellipsis;
        width: 100%;

        .artist-info {
            border-bottom: 2px solid #262626;
            height: 60%;
            width: 100%;

            .artist-name {
                font-size: 32px;
                overflow: hidden;
                height: 60%;
            }

            .event-name {
                font-size: 16px;
                overflow: hidden;
                height: 30%;
            }
        }

        .sub-info {
            display: flex;
            flex-direction: row;
            height: 40%;
            justify-content: baseline;

            .date-info {
                display: inline-block;
                border-right: 2px solid #262626;
                flex-direction: column;
                justify-content: center;
                height: 100%;
                width: 40%;

                .month {
                    align-items: center;
                    display: flex;
                    font-size: 13px;
                    justify-content: center;
                }

                .inner-date {
                    align-items: center;
                    display: flex;
                    width: 100%;
                    flex-direction: row;
                    justify-content: center;
                    gap: 4px;

                    .day {
                        display: flex;
                        font-size: 26px;
                    }

                    .year {
                        font-size: 13px;
                        height: 100%;
                        word-wrap: break-word;
                        width: 20px;
                    }
                }
            }

            .venue-info {
                align-items: center;
                display: flex;
                flex-direction: column;
                justify-content: center;
                width: 60%;

                .venue-name {
                    display: inline-block;
                    font-size: 15px;
                    overflow: wrap;
                }

                .venue-city {
                    display: inline-block;
                    font-size: 11px;
                }
            }
        }
    }

    .ticket-stub {
        align-items: center;
        background-color: #F48722;
        border-left: 3px dashed #262626;
        display: flex;
        flex-direction: row;
        height: 100%;
        width: 65px;

        p {
            display: inline-block;
            line-height: 9px;
            font-size: 9px;
            margin: 0;
            padding: 3px;
            writing-mode: vertical-lr;
            transform: rotate(180deg);
        }
        
        .ticket-border {
            align-items: center;
            background-color: #FDF4EC;
            display: flex;
            justify-content: center;
            flex-direction: row;
            width: 15px;
            height: 100%;
        }
    }
}